import {Component, Input, ChangeDetectionStrategy, ContentChild, Optional, ElementRef, Renderer, TemplateRef, OnChanges} from '@angular/core';
import {NglFormGroupAlternate} from './group-alt';
import {NglFormCheckbox} from '../elements/input';
import {NglFormLabelTemplate, getFormLabel} from '../form-label';
import {uniqueId} from '../../util/util';


export class NglFormGroupElement implements OnChanges {
   contentEl: NglFormCheckbox;

   labelStr: string;
   labelTpl: NglFormLabelTemplate;

  _label: TemplateRef<any> | string;

  uid = uniqueId('form_element');

  get labelClass() {
    return `slds-${this.contentEl.type}${this.groupAlt ? '--button' : ''}__label`;
  }

  constructor( private groupAlt: NglFormGroupAlternate, private element: ElementRef, private renderer: Renderer) {}

  ngOnChanges(changes?: any) {
    this.setFormLabel();
  }

  ngAfterContentInit() {
    if (!this.contentEl) {
      throw Error(`Couldn't find an input radio or checkbox with [nglFormControl] attribute inside <ngl-form-group-element>`);
    }

    const { type } = this.contentEl;

    if (this.groupAlt) {
      this.groupAlt.type = type;
      this.renderer.setElementClass(this.element.nativeElement, 'slds-button' , true);
      this.renderer.setElementClass(this.element.nativeElement, `slds-${type}--button`, true);
    } else {
      this.renderer.setElementClass(this.element.nativeElement, `slds-${type}`, true);
    }

    this.contentEl.id = this.uid;
    this.setFormLabel();
  }

  private setFormLabel() {
    this._label = getFormLabel(this.labelStr, this.labelTpl);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ngl-form-group-element',
  template: `
    <ng-content></ng-content><label *ngIf="contentEl" [ngClass]="labelClass" [attr.for]="uid" [ngSwitch]="!!groupAlt"><ng-container *ngSwitchCase="false"><span [ngClass]="'slds-' + contentEl.type + '--faux'"></span><span class="slds-form-element__label" [nglInternalOutlet]="_label"></span></ng-container><ng-container *ngSwitchCase="true"><span [ngClass]="'slds-' + contentEl.type + '--faux'" [nglInternalOutlet]="_label"></span></ng-container></label>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: NglFormGroupAlternate, decorators: [{ type: Optional }, ]},
{type: ElementRef, },
{type: Renderer, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'contentEl': [{ type: ContentChild, args: [NglFormCheckbox, ] },],
'labelStr': [{ type: Input, args: ['label', ] },],
'labelTpl': [{ type: ContentChild, args: [NglFormLabelTemplate, ] },],
};
};

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
